<template>
  <div class="collection">
    <topHead
      :formId="formId"
      @deleteClick="deleteClick"
      @nullClick="nullClick"
      @addFormClick="addFormClick"
      @submittedClick="submittedClick"
      @fankuiClick="fankuiClick"
    ></topHead>
    <topSelect :formId="formId" @selectClick="selectClick"></topSelect>
    <div style="border: solid 1px #e0e0e0;margin: 0 10px;">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table
          :data="payContinueTable"
          border
          style="max-width:100%;border: none"
          @selection-change="selsChange"
          :ref="formId+'Table'"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column prop="employmentNumber" label="工号"></el-table-column>
          <el-table-column prop="personName" label="姓名"></el-table-column>
          <el-table-column prop="cardType" label="证照类型"></el-table-column>
          <el-table-column prop="cardNumber" label="证照号码" show-overflow-tooltip></el-table-column>
          <el-table-column prop="isBaosong" label="报送状态"></el-table-column>
          <el-table-column prop="isShenfen" label="身份验证状态"></el-table-column>
          <el-table-column prop="address" label="操作">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="updataRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column prop="address" label="更新日期">
            <el-input :value="company[0].time_start"></el-input>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 新增和修改表的弹窗 -->
    <el-dialog
      class="in-table"
      title="新增继续教育支出信息"
      :visible.sync="centerDialogVisible"
      width="1000px"
      top="10vh"
    >
      <div class="in-table-content">
        <!-- 表单开始的地方 -->
        <el-form
          :status-icon="true"
          :model="payContinueForm"
          :rules="rules"
          ref="payContinueForm"
          label-width="80px"
          class="demo-payContinueForm"
          label-position="right"
          size="mini"
        >
          <!-- 弹窗所有的表格信息 限定为600px高度-->
          <div style="height:600px;padding:0 30px 0 0px; overflow-y:auto">
            <div class="pl-20 mt-5 pb-10 line-bottom">
              <div class="mb-5">
                <span class="content-top-text-blue"></span>
                <span class="title text-red" style="line-height:40px;">温馨提示</span>
              </div>
            </div>
            <div class="ml-50 text-notice">1.一个纳税年度内，学历(学位)继续教育支出和职业资格继续教育支出可同时享受扣除；</div>
            <div class="ml-50 text-notice">2.一个纳税年度内，参加多项学历教育的不得叠加享受，取得多本职业资格继续教育证书的不得叠加享受；</div>
            <!-- 纳税个人信息 -->
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left:2px">纳税人信息</span>
              </div>

              <div style="display:flex">
                <el-form-item v-if="isUpdata" label="姓名" style="width:33%">
                  <el-input :value="editValue" readonly></el-input>
                </el-form-item>
                <el-form-item v-else label="姓名" style="width:33%" prop="personInfoId">
                  <el-select v-model="payContinueForm.personInfoId" placeholder="-请选择姓名-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      :key="i"
                      :label="item.personName"
                      :value="item.infoId"
                    ></el-option>
                  </el-select>
                </el-form-item>
                <el-form-item label="证件类型" style="width:33%">
                  <el-input value="居民身份证" ></el-input>
                </el-form-item>
                <el-form-item label="证件号码" style="width:33%" prop="cardNumber">
                  <el-select v-model="payContinueForm.cardNumber" placeholder="-请选择证件号码-">
                    <el-option
                      v-for="(item, i) in personInfo"
                      :key="i"
                      :label="item.cardNumber"
                      :value="item.cardNumber"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </div>
            </div>
            <!-- 教育支出信息 -->
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">学历（学位）继续教育情况</span>
              </div>
            </div>
            <el-button size="small" type="primary" @click="addEducationTableCol">新增</el-button>
            <el-button size="small" @click="deleteEducationTableCol">删除</el-button>
            <!-- 学历教育支出信息的表格 -->
            <el-table :data="payContinueForm.tableEducationPayData" border style="width: 100%;margin-top: 5px">
              <el-table-column label="序号" prop="number" width="50"></el-table-column>
              <el-table-column prop="name" width="200" label="入学时间起">
                <template slot-scope="scope">
                  <el-date-picker
                    type="date"
                    v-model="scope.row.learnStartDay"
                    placeholder
                    style="width: 100%;"
                    value-format="yyyy-MM-dd"
                  ></el-date-picker>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="200" label="（预计）毕业时间">
                <template slot-scope="scope">
                  <el-date-picker
                    type="date"
                    v-model="scope.row.learnEndDay"
                    placeholder
                    style="width: 100%;"
                    value-format="yyyy-MM-dd"
                  ></el-date-picker>
                </template>
              </el-table-column>
              <el-table-column width="200" label="教育阶段">
                <template slot-scope="scope">
                  <el-select v-model="scope.row.learnStage">
                    <el-option value="大学专科"></el-option>
                    <el-option value="大学本科"></el-option>
                    <el-option value="硕士研究生"></el-option>
                    <el-option value="博士研究生"></el-option>
                    <el-option value="其他"></el-option>
                  </el-select>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="150" label="扣除有效期起">
                <el-input :value="company[0].time_start" readonly></el-input>
              </el-table-column>
              <el-table-column prop="address" width="150" label="扣除有效期止">
                <el-input :value="company[0].time_end" readonly></el-input>
              </el-table-column>
              <el-table-column prop="address" width="120" label="采集来源">
                <template>
                  <el-input value="扣缴义务人"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="120" label="更新日期">
                <template>
                  <el-input :value="company[0].time_start"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="90" label="状态">
                <template>
                  <el-input value="待报送"></el-input>
                </template>
              </el-table-column>
            </el-table>
            <div class="main-info">
              <div style="height:40px;line-height:40px;position:relative;left:5px">
                <span class="content-top-text-blue"></span>
                <span style="margin-left: 2px">职业资格继续教育情况</span>
              </div>
            </div>
            <el-button size="small" type="primary" @click="addQualificationTableCol">新增</el-button>
            <el-button size="small" @click="deleteQualificationTableCol">删除</el-button>
            <el-table :data="payContinueForm.tableQualificationPayData" border style="width: 100%;margin-top: 5px">
              <el-table-column prop="number" label="序号" width="50"></el-table-column>
              <el-table-column prop="name" width="200" label="继续教育类型">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.continueLearnStage"></el-input>
                </template>
                <template slot-scope="scope">
                  <el-select v-model="scope.row.continueLearnStage">
                    <el-option value="技能人员职业资格"></el-option>
                    <el-option value="专业技术人员人员职业资格"></el-option>
                  </el-select>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="200" label="发证（批准）日期">
                <template slot-scope="scope">
                  <el-date-picker
                    type="date"
                    v-model="scope.row.certificateTime"
                    placeholder
                    style="width: 100%;"
                    value-format="yyyy-MM-dd"
                  ></el-date-picker>
                </template>
              </el-table-column>
              <el-table-column width="200" label="证书名称">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.certificateName"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="200" label="证书编号">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.certificateId"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" label="发证机关">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.certificateOffice"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="150" label="扣除有效期起">
                <el-input :value="company[0].time_start" readonly></el-input>
              </el-table-column>
              <el-table-column prop="address" width="150" label="扣除有效期止">
                <el-input :value="company[0].time_end" readonly></el-input>
              </el-table-column>
              <el-table-column prop="address" width="160" label="受教育日期止">
                <template slot-scope="scope">
                  <el-date-picker type="date" v-model="scope.row.learnEndDay" placeholder style="width: 100%;"
                                  value-format="yyyy-MM-dd"></el-date-picker>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="120" label="采集来源">
                <template>
                  <el-input value="扣缴义务人"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="120" label="更新日期">
                <template>
                  <el-input :value="company[0].time_start"></el-input>
                </template>
              </el-table-column>
              <el-table-column prop="address" width="90" label="状态">
                <template>
                  <el-input value="待报送"></el-input>
                </template>
              </el-table-column>
            </el-table>
          </div>
          <el-form-item class="btn-right">
            <el-button type="primary" size="small" @click="submitForm('payContinueForm')">保存</el-button>
            <el-button size="small" @click="resetForm('payContinueForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import formHelper from '../../until/formHelper'
import dataHelper from '../../until/dataHelper'
import commonData from '../../until/commonData'
import topHead from './components/top-head'
import topSelect from './components/top-select'
import { mapGetters } from 'vuex'

var tableName = 'payContinueForm'
export default {
  components: { topHead, topSelect },
  data () {
    return {
      ...commonData,
      formId: tableName,
      isIn: true,
      tableDataIn: [], // 境内人员的表格信息
      selectStatus: [],
      selectCollection: { // 查询的信息
        texPlayerNumber: '',
        employmentNumber: '',
        personName: '',
        cardNumber: '',
        isShenfen: '',
        isBaosong: '',
        porsonStatus: '',
        isCangulie: '',
        employmentType: ''
      },
      payContinueForm: {// 表单的信息
        cardNumber: null,
        personInfoId: '',
        tableEducationPayData: [],
        isBaosong: '未报送',
        isShenfen: '未验证',
        tableQualificationPayData: []
      },
      bodySelect: {
        user: '',
        region: ''
      },
      rules: {
        personInfoId: [
          { required: true, message: '请选择姓名', trigger: 'change' }
        ],
        cardNumber: [
          { required: true, message: '请选择身份证号', trigger: 'blur' }
        ]
      },
      editValue: ''
    }
  },
  watch: {
    centerDialogVisible (val) {
      if (!val) {
        this.isUpdata = false
        this[tableName] = this.$options.data()[tableName]
      }
    }
  },
  computed: {
    ...mapGetters(['company']),
    personInfo () {
      return dataHelper.tableRelevance(this, 'personCollection', ['personName', 'cardNumber', 'cardType', 'infoId'], tableName)
    },
    payContinueTable: {
      get () {
        if (!this.isSelectNow) {
          let b = dataHelper.tableRelevanceArr(this, this.$store.state.form[tableName], this.$store.getters.getFormContent('personCollection'), ['personName', 'cardNumber', 'cardType', 'employmentNumber'], 'personInfoId', 'infoId')
          return b
        } else {
          return this.selectArr
        }
      }
    }
  },
  methods: {
    ...formHelper,
    addFormClick () {
      this.centerDialogVisible = true
    },
    submitForm (formName) {
      var _this = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let submitData = {} // 用于提交的主键，格式参考store
          _this[formName].infoId = _this.$store.getters.getFormInfoId(formName) + 1
          submitData.formId = formName
          submitData.content = _this[formName]
          if (_this.isUpdata) {
            _this.$store.commit('updataFormInfo', submitData)
            _this.isUpdata = false
          } else {
            _this.$store.commit('addFormInfo', submitData)
          }

          _this.centerDialogVisible = false
          this[formName] = this.$options.data()[formName]
          _this.$message.success('提交成功!')
          console.log(this.$store.getters.getFormContent(tableName))
        } else {
          return false
        }
      })
    },
    handleCheckAllChange () {
      this.selectStatus = ['待报送', '待反馈', '报送成功', '报送失败']
    },
    handCancle () {
      this.selectStatus = []
    },
    updataRow (a, b) {
      this.centerDialogVisible = true
      this.payContinueForm = this.$store.getters.getFormOneInfo(tableName, b.infoId)
      this.editValue = this.payContinueForm.personName
      this.isUpdata = true
    },
    addEducationTableCol () {
      this.payContinueForm.tableEducationPayData.push({
        number: this.payContinueForm.tableEducationPayData.length + 1,
        learnStartDay: '',
        learnEndDay: '',
        learnStage: ''
      })
    },
    deleteEducationTableCol () {
      this.payContinueForm.tableEducationPayData.pop()
    },
    addQualificationTableCol () {
      this.payContinueForm.tableQualificationPayData.push({
        number: this.payContinueForm.tableQualificationPayData.length + 1,
        learnStartDay: '',
        learnEndDay: '',
        learnStage: ''
      })
    },
    deleteQualificationTableCol () {
      this.payContinueForm.tableQualificationPayData.pop()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .collection {
    font-size: 12px;
    text-align: start;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 35px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    border: solid 1px #e0e0e0;
    padding: 0 10px;
    margin: 0 35px 0 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 0 0 30px;
  }

  .has-gutter th .cell {
    line-height: 30px !important;
  }

  .el-form-item {
    margin-bottom: 5px;
  }
</style>
<style>
  .el-button + .el-button {
    margin: 0;
  }

  .el-dialog__body {
    padding: 0;
  }

  .person-status .el-form-item__label {
    position: relative;
    float: right;
    right: 180px;
  }

  .person-status.el-form-item {
    margin-bottom: 5px;
  }

  .el-form-item {
    margin-bottom: 18px;
  }

  .el-input__inner {
    height: 30px;
  }

  .el-form-item__label {
    width: 120px;
  }

  .el-select {
    width: 100%;
  }

  .el-form-item__label {
    font-size: 12px;
  }

  .top-btn .el-form-item__content {
    position: relative;
    right: 500;
    left: 500;
  }

  .el-form-item.el-form-item--feedback.el-form-item--mini {
    width: 294.69px;
  }
</style>



